.profile{
    max-width: 880px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-top: 37px;
    margin: 0 auto;

}
@media screen and (max-width: 768px){
    .profile{
        flex-direction: column;
        max-width: 580px;
    }
}
@media screen and (max-width: 485px){
    .profile{
        max-width: 282px;
        flex-direction: column;
        padding-top: 42px;
    }
}

